.pqge-grid-2 {
	pointer-events: none;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	height: 100vh;
	height: 100%;

	&__content {
		margin: 0 auto;
		padding: 0 50px;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 40px;
		box-sizing: border-box;

		@include _screen-1440 {
			grid-template-columns: repeat(2, 1fr);
			padding: 0 30px;
			gap: 30px;
		}

		@include _screen-960 {
		}

		@include _screen-690 {
			grid-template-columns: repeat(1, 1fr);
		}

		@include _screen-490 {
			padding: 0 10px;
			gap: 10px;
		}
	}

	&__column {
		width: 100%;
		height: 100%;
		// background: #0026ff1a;

		@include _screen-1440 {
			&:nth-child(3) {
				display: none;
			}
		}

		@include _screen-690 {
			&:nth-child(2),
			&:nth-child(3) {
				display: none;
			}
		}
	}
}

.page-grid-3 {
	pointer-events: none;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	height: 100vh;

	&__content {
		margin: 0 auto;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 60px;
		padding: 0 50px;
		box-sizing: border-box;

		@include _1440-1920 {
			gap: calc(20px + (60 - 20) * ((100vw - 1440px) / (1920 - 1440)));
			padding: 0 calc(30px + (50 - 30) * ((100vw - 1440px) / (1920 - 1440)));
		}

		@include _screen-1440 {
			grid-template-columns: repeat(2, 1fr);
			gap: 20px;
		}

		@include _screen-1440 {
			padding: 0 30px;
		}

		@include _screen-690 {
			grid-template-columns: repeat(1, 1fr);
		}

		@include _screen-375 {
			padding: 0 10px;
			gap: 10px;
		}
	}

	&__column {
		width: 100%;
		height: 100%;
		background: #ff6f4336;

		@include _screen-1440 {
			&:nth-child(3) {
				display: none;
			}
		}

		@include _screen-690 {
			&:nth-child(2),
			&:nth-child(3) {
				display: none;
			}
		}
	}
}
.pqge-grid-4 {
	pointer-events: none;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	height: 100%;

	&__content {
		margin: 0 auto;
		padding: 0 50px;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 40px;
		box-sizing: border-box;

		@include _screen-1440 {
			grid-template-columns: repeat(3, 1fr);
			padding: 0 30px;
			gap: 20px;
		}

		@include _screen-960 {
			grid-template-columns: repeat(2, 1fr);
		}

		@include _screen-690 {
			grid-template-columns: repeat(1, 1fr);
		}

		@include _screen-490 {
			padding: 0 10px;
			gap: 10px;
		}
	}

	&__column {
		width: 100%;
		height: 100%;
		background: #0026ff1a;

		@include _screen-1440 {
			&:nth-child(4) {
				display: none;
			}
		}

		@include _screen-960 {
			&:nth-child(3),
			&:nth-child(4) {
				display: none;
			}
		}

		@include _screen-690 {
			&:nth-child(2),
			&:nth-child(3),
			&:nth-child(4) {
				display: none;
			}
		}
	}
}
