.performance {
  width: 100%;
  background: #171b1c;
}
.performance__body {
  position: relative;
  padding-top: 21.6rem;
  padding-bottom: 14.7rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 60px;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: url("../img/performance/ellipse-01.png") top -15% left 17%/50% no-repeat, url("../img/performance/ellipse-02.png") top -6% right -7%/25% no-repeat;
}
@media (min-width: 490.99px) {
  .performance__body::before {
    content: "";
    position: absolute;
    width: 250%;
    height: 250%;
    top: -40%;
    left: -50%;
    z-index: 4;
    pointer-events: none;
    transform: rotate(90deg);
    background: radial-gradient(ellipse 70% 290% at 30% 80%, rgba(22, 22, 22, 0.6352941176) 0%, rgba(0, 0, 0, 0.2862745098) 10%, transparent 45%), radial-gradient(circle at 40% 29%, rgba(110, 123, 177, 0.9254901961) 1%, rgba(43, 55, 105, 0.4745098039) 14%, transparent 25%), radial-gradient(ellipse 74% 95% at 45% 85%, #6e7bb1 0%, rgba(43, 55, 105, 0.3764705882) 13%, transparent 25%);
  }
  .performance__body::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    width: 250%;
    height: 250%;
    pointer-events: none;
    transform: rotate(-20deg);
    background: radial-gradient(ellipse 30% 30% at 27% 22%, #2b3769 0%, rgba(43, 55, 105, 0.4745098039) 30%, transparent 60%);
  }
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .performance__body {
    column-gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
